import axios from 'axios';

export const HTTP = axios.create({
  // baseURL: 'http://129.9.0.250:9998/lgup-tsdn/', 
  baseURL: 'https://web-gui-714ec.firebaseio.com/'
    // responseType: 'json',
    // headers: {
    //   'Accept': 'application/json',
    //   'Content-Type': 'application/json'
    // },
    // headers: { 'Access-Control-Allow-Origin': true },
    // withCredentials: true,
    // credentials: 'same-origin'
})

HTTP.discoveryNodes = HTTP.get('discovery/nodes.json');
HTTP.discoveryLinks = HTTP.get('discovery/links.json');
HTTP.discoveryTunnels = HTTP.get('discovery/tunnels.json');
HTTP.requestTunnel = (id) => HTTP.get(`tunnel/${id}.json`);


